<template>
  <div class="detail-nav-bar">
    <nav-bar>
      <div slot="left" class="back" @click="backClick">
        <img src="~assets/img/common/back.svg" alt="">
      </div>
      <div slot="center" class="title">
      <div v-for="(item,index) in titles" class="title-item" :class="{active: index === currentIndex}" @click="titleClick(index)" :key="index">{{item}}</div></div></nav-bar>
  </div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'

export default {
  name:"DetailNavBar",
  components:{
    NavBar
  },
  data(){
    return {
      titles:['商品','参数','评论','推荐'],
      currentIndex:0
    }
  },
  methods: {
    titleClick(index){
      this.$emit('titleClick', index)
    },
    backClick(){
      this.$router.back()
    }
  },
}
</script>

<style>
.detail-nav-bar{
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: #fff;
}
 .title{
   display: flex;
   
 }
 .title-item{
   flex: 1;
 }

 .active{
   color: var(--color-high-text);
 }

 .back img{
   vertical-align: middle;
 }
</style>